.navigator-conteiner > *{
    background-color: white;
}
.navigaror-panel-conteiner{
    border: 1px solid #D5D8DD;
    border-top: 0;
    position: sticky;
    top: 51px;
    display: flex;
    flex-wrap: nowrap;
}
.navigaror-panel-conteiner > *{
    height: 100vh;
}

.navigaror-panel{
    width: 56px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
}
.navigator-conteiner{
    position: relative;
    z-index: 2;
}
.absolute-pos{
    position: absolute;
    left: 56px;
}
.static-pose{
    border-right: 0 !important;
    position: static;
}
.tree-panel{
    width: 280px;
    background-color: white;
    border: 1px solid #D5D8DD;
    border-top:0;
}
.navigaror-panel-icon{
    cursor:pointer;
    flex:0;
    margin: 16px;
}
.tree-panel-pin{
    height: 20px;
    width: 20px;
    rotate: 45deg;
}
.nav-header{
    display: flex;
    height: 65px;
    width: 278px;
    padding: 16px;
    border-bottom: 1px solid #D5D8DD;
    gap: 8px;
}
.nav-header input::placeholder{
    color: #D5D8DD;
} 
.tree-panel-cont-conteiner{
    padding: 10px;
    background-color: #FCFCFD;
    height: 40px;
    width: 278px;
} 
.tree-panel-cont-conteiner span{
    color: #2E3238;
}
.arrow{
    border-top: 2px solid #ABB1BA;
    border-right: 2px solid #ABB1BA;
    rotate: 45deg;
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-right: 8px;
    cursor: pointer;
}